header {
  width: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(#75d8c8), to(#2f9e9c));
  background: -webkit-linear-gradient(#75d8c8, #2f9e9c);
  background: -o-linear-gradient(#75d8c8, #2f9e9c);
  background: linear-gradient(#75d8c8, #2f9e9c);
  margin-bottom: 20px;
}

header .textholder {
  width: 80%;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

header h2 {
  color: #ffffff;
  letter-spacing: 2px;
  font-size: 22px;
  margin-left: 10px;
  margin-right: 10px;
}

header .icon {
  width: 20px;
  height: 20px;
  background-image: url("star.png");
  background-size: contain;
  background-repeat: no-repeat;
}



